import pageHeader from '../../../components/pageheader/pageHeader.vue';
import shopLists from '../../../components/shopLists/shopLists.vue';
import interfaces from '../../../utils/interfaces.js';

export default {
	data() {
		return {
			swipeImgs: [],
			entries: [],
			shopList: [],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			circular: true,
			page: 1,
			size: 5,
			allLoaded: false
		};
	},
	onLoad() {
		this.initData();
		this.shopLists();
	},
	components: {
		pageHeader,
		shopLists
	},
	onReachBottom() {
		if (!this.allLoaded) {
			this.page++;
			this.shopLists();
		}
	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getBanner,
				success: res => {
					// console.log(res);
					this.swipeImgs = res.swipeImgs;
					this.entries = res.entries;
				}
			});
		},
		shopLists() {
			this.allLoaded = false;

			this.request({
				url: interfaces.getShoplists + '/' + this.page + '/' + this.size,
				// url: interfaces.getShoplists,
				data: {
					page: this.page,
					size: this.size
				},
				method: 'POST',
				success: res => {
					console.log(res);
					// this.shopList = res;
					if (res.length >= this.size) {
						res.forEach(item => {
							this.shopList.push(item);
						})
					} else {
						this.allLoaded = true;
					}
				}
			});
		}
	}
};
